@import url(https://fonts.googleapis.com/css?family=Charmonman);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.search {
  margin: 3% 20%; }
  .search .search-bar input {
    padding: 10px 20px;
    width: 100%;
    font-size: 1.5em;
    box-sizing: border-box; }
  .search .search-bar p {
    text-align: center;
    color: grey; }
  .search .search-tags {
    display: flex;
    margin: 20px;
    flex-wrap: wrap;
    justify-content: center; }
    .search .search-tags .search-tag {
      background: #4A90E2;
      padding: 5px 10px 7.5px 10px;
      border-radius: 3px;
      border: none;
      margin: 0 10px;
      color: white; }
      .search .search-tags .search-tag a {
        margin-left: 15px;
        cursor: pointer; }

.search-results {
  display: flex;
  flex-wrap: wrap;
  padding: 3% 10%;
  justify-content: center; }
  .search-results .search-result {
    border: 1px solid grey;
    margin: 50px 20px;
    flex: 0 1 30%; }
    .search-results .search-result img {
      width: 100%; }

header {
  background: #deeff5;
  padding-bottom: 50px; }

.Home .new-feature {
  display: none; }

input:not([type="checkbox"]), textarea, button {
  display: block;
  width: 100%;
  padding: 15px 20px;
  margin: 30px 0; }

textarea {
  min-height: 100px; }

a {
  transition: 0.5s all ease;
  cursor: pointer; }
  a:hover {
    color: #444; }

nav {
  padding: 25px 10px; }
  nav div {
    float: right; }
  nav a {
    margin: 0 30px;
    color: grey;
    text-decoration: none;
    border-bottom: 1px solid grey;
    padding-bottom: 3px; }
  nav .logo {
    font-family: 'Charmonman', cursive;
    border: none;
    font-size: 2em; }

header {
  overflow: hidden; }

.outfit-slider {
  height: 500px; }
  .outfit-slider .outfit-slides {
    display: flex;
    transition: 0.5s all ease; }
  .outfit-slider .slide-left-link {
    position: -webkit-sticky;
    position: sticky;
    top: 300px;
    left: 3vw;
    cursor: pointer;
    border: none;
    z-index: 999; }
  .outfit-slider .slide-right-link {
    position: -webkit-sticky;
    position: sticky;
    top: 300px;
    left: 97vw;
    cursor: pointer;
    border: none;
    z-index: 999; }

.outfit-slide {
  height: 500px;
  flex: 1 0 100vw;
  text-align: center;
  position: relative; }
  .outfit-slide a {
    color: grey;
    border-bottom: 1px solid grey;
    padding-bottom: 3px;
    text-decoration: none; }
  .outfit-slide .details-link {
    display: inline-block; }
  .outfit-slide .outfit-info {
    position: absolute;
    bottom: 5%;
    left: 5%;
    width: 30%;
    text-align: left;
    z-index: 999; }
  .outfit-slide .image-container {
    height: 100%;
    display: inline-block;
    margin: 0 auto;
    position: relative; }
    .outfit-slide .image-container img {
      max-width: 80vw;
      height: 100%; }

.outfit-features {
  position: absolute;
  margin: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }
  .outfit-features .outfit-feature {
    position: absolute;
    width: 30px;
    height: 30px;
    line-height: 30px;
    background: #4A90E2;
    color: white;
    border-radius: 50%;
    text-align: center; }
    .outfit-features .outfit-feature .tooltip {
      display: none;
      position: absolute;
      top: 0;
      left: 100%;
      text-align: left;
      background: white;
      color: black;
      line-height: auto;
      padding: 10px 30px 30px 20px;
      margin-left: 10px;
      min-width: 200px; }
    .outfit-features .outfit-feature.open .tooltip {
      display: block; }

.new-feature-canvas,
.new-feature-form-container {
  position: absolute;
  margin: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.new-feature-module {
  position: absolute;
  top: 0;
  left: 100%; }
  .new-feature-module button {
    margin: 0;
    width: 150px;
    padding: 3px 5px; }

.new-feature-canvas {
  background: rgba(0, 0, 0, 0.5); }

.feature-list {
  padding: 5% 10%; }
  .feature-list .feature {
    margin-bottom: 50px; }
  .feature-list a {
    color: grey;
    border-bottom: 1px solid grey;
    padding-bottom: 3px;
    text-decoration: none; }

.feature-tag-list {
  display: flex; }
  .feature-tag-list .tag {
    padding: 5px 10px;
    margin-right: 20px;
    background: #4A90E2;
    color: white; }
    .feature-tag-list .tag:before {
      content: '#'; }

.new-outfit-page .form-container {
  text-align: center; }
  .new-outfit-page .form-container img {
    max-width: 300px;
    margin: 40px; }
  .new-outfit-page .form-container form {
    width: 60%;
    margin: 0 auto; }
    .new-outfit-page .form-container form textarea {
      min-height: 200px; }

